.app {
  font-family: Arial, sans-serif;
  margin: 20px;
  text-align: center;
}

table {
  margin: 0 auto;
  border-collapse: collapse;
  width: 80%;
}

th, td {
  border: 1px solid #ccc;
  padding: 10px;
  text-align: center;
}

select {
  padding: 5px;
  border-radius: 5px;
  border: 1px solid #ccc;
}
